import { storage } from '@guardian/libs';
const KEY = 'gu.brazeUserSet';
const hasCurrentBrazeUser = () => {
    return storage.local.getRaw(KEY) === 'true';
};
const setHasCurrentBrazeUser = () => {
    storage.local.setRaw(KEY, 'true');
};
const clearHasCurrentBrazeUser = () => {
    storage.local.remove(KEY);
};
export { hasCurrentBrazeUser, setHasCurrentBrazeUser, clearHasCurrentBrazeUser, };
