import { BrazeCards, BrazeMessages, canRenderBrazeMsg, LocalMessageCache, NullBrazeCards, NullBrazeMessages, } from '@guardian/braze-components/logic';
import { log, startPerformanceMeasure, storage } from '@guardian/libs';
import { getOphan } from '../../client/ophan/ophan';
import { clearHasCurrentBrazeUser, hasCurrentBrazeUser, setHasCurrentBrazeUser, } from '../hasCurrentBrazeUser';
import { checkBrazeDependencies } from './checkBrazeDependencies';
import { getInitialisedAppboy } from './initialiseAppboy';
const maybeWipeUserData = async (apiKey, brazeUuid, consent, brazeSwitch) => {
    const hasCurrentBrazeUserValue = hasCurrentBrazeUser();
    const userHasLoggedOut = !brazeUuid && hasCurrentBrazeUserValue;
    const userHasRemovedConsent = !consent && hasCurrentBrazeUserValue;
    const brazeHasBeenDisabled = !brazeSwitch && hasCurrentBrazeUserValue;
    if (userHasLoggedOut || userHasRemovedConsent || brazeHasBeenDisabled) {
        try {
            if (apiKey) {
                const appboy = await getInitialisedAppboy(apiKey);
                appboy.wipeData();
            }
            LocalMessageCache.clear();
            clearHasCurrentBrazeUser();
        }
        catch (error) {
            window.guardian.modules.sentry.reportError(
            // @ts-expect-error
            error, 'braze-maybeWipeUserData');
        }
    }
};
export const buildBrazeMessaging = async (idApiUrl, isSignedIn, renderingTarget) => {
    if (!storage.local.isAvailable()) {
        // we require local storage for using any message channel so that we know
        // when to clear up user data from the device on logout
        return {
            brazeMessages: new NullBrazeMessages(),
            brazeCards: new NullBrazeCards(),
        };
    }
    const dependenciesResult = await checkBrazeDependencies(isSignedIn, idApiUrl);
    if (!dependenciesResult.isSuccessful) {
        const { failure, data } = dependenciesResult;
        log('tx', `Not attempting to show Braze messages. Dependency ${failure.field} failed with ${String(failure.data)}.`);
        await maybeWipeUserData(data.apiKey, data.brazeUuid, data.consent, data.brazeSwitch);
        return {
            brazeMessages: new NullBrazeMessages(),
            brazeCards: new NullBrazeCards(),
        };
    }
    try {
        const { endPerformanceMeasure } = startPerformanceMeasure('tx', 'braze-sdk-load');
        const appboy = await getInitialisedAppboy(dependenciesResult.data.apiKey);
        const sdkLoadTimeTaken = endPerformanceMeasure();
        const ophan = await getOphan(renderingTarget);
        ophan.record({
            // @ts-expect-error -- the relevant team should remove this call as it is dropped by Ophan
            // see https://github.com/guardian/dotcom-rendering/pull/11438 further context
            component: 'braze-sdk-load-timing',
            value: sdkLoadTimeTaken,
        });
        const errorHandler = (error, desc) => {
            window.guardian.modules.sentry.reportError(error, desc);
        };
        setHasCurrentBrazeUser();
        appboy.changeUser(dependenciesResult.data.brazeUuid);
        appboy.openSession();
        const brazeCards = window.guardian.config.switches.brazeContentCards
            ? new BrazeCards(appboy, errorHandler)
            : new NullBrazeCards();
        const brazeMessages = new BrazeMessages(appboy, LocalMessageCache, errorHandler, canRenderBrazeMsg);
        return { brazeMessages, brazeCards };
    }
    catch {
        return {
            brazeMessages: new NullBrazeMessages(),
            brazeCards: new NullBrazeCards(),
        };
    }
};
