import { getIdapiUserIdentifiers } from './getIdapiUserData';
import { eitherInOktaTestOrElse } from './identity';
export const getBrazeUuid = async (ajaxUrl) => 
// TODO Okta: Remove either when at 100% in oktaVariant test, and just use idToken
eitherInOktaTestOrElse((authState) => authState.idToken?.claims.braze_uuid, () => getIdapiUserIdentifiers(ajaxUrl)
    .then((data) => data.brazeUuid)
    .catch((error) => {
    window.guardian.modules.sentry.reportError(error, 'getBrazeUuid');
}));
